/* Fonts */
@font-face {
  font-family: Roboto;
  src: url("/css/fonts/Roboto-Light.ttf");
  font-weight: lighter;
}

@font-face {
  font-family: Roboto;
  src: url("/css/fonts/Roboto-Regular.ttf");
}

@font-face {
  font-family: Roboto;
  src: url("/css/fonts/Roboto-Bold.ttf");
  font-weight: bold;
}

@font-face {
  font-family: RobotoCondensed;
  src: url("/css/fonts/RobotoCondensed-Light.ttf");
  font-weight: lighter;
}

@font-face {
  font-family: RobotoCondensed;
  src: url("/css/fonts/RobotoCondensed-Regular.ttf");
}

@font-face {
  font-family: RobotoCondensed;
  src: url("/css/fonts/RobotoCondensed-Bold.ttf");
  font-weight: bold;
}

/* Media Queries */
body,
html {
  margin: 0;
  padding: 0;
}

body {
  font-family: "RobotoCondensed";
  font-size: 16px;
  line-height: 1.5em;
}

a {
  text-decoration: none;
  color: inherit;
}

nav {
  background-color: #ce181f;
  display: grid;
  grid-template-columns: 5fr auto auto;
  height: 48px;
  position: fixed;
  top: 0;
  width: 100%;
}

@media only screen and (min-width: 768px) {
  nav {
    grid-template-columns: 1fr 10fr 1fr;
    height: auto;
  }
}

nav img {
  -ms-flex-item-align: center;
  align-self: center;
  height: 36px;
  margin-left: 4px;
}

@media only screen and (min-width: 768px) {
  nav img {
    display: none;
  }
}

nav .active {
  background-color: #e6e7e8 !important;
}

nav .active i,
nav .active p {
  color: #2F2F2F !important;
}

nav .mobile-button {
  background-color: #2F2F2F;
  cursor: pointer;
  display: grid;
  padding: 6px 0;
}

@media only screen and (min-width: 768px) {
  nav .mobile-button {
    display: none;
  }
}

nav .mobile-button .mobile-navigation {
  color: #fff;
  display: grid;
  justify-items: center;
  grid-gap: 10px;
  padding-left: 10px;
}

nav .mobile-button .mobile-navigation p {
  font-size: 12px;
  line-height: 0;
  padding-bottom: 5px;
  text-transform: uppercase;
  margin: 0;
}

nav .mobile-button .mobile-navigation i {
  font-size: 24px;
  padding-top: 2px;
}

nav .navigation {
  background-color: #e6e7e8;
  position: fixed;
  right: 0;
  top: 48px;
  height: calc(100% - 47px);
  margin-bottom: 150px;
  overflow-y: auto;
  -webkit-transition-duration: 1s;
  transition-duration: 1s;
  width: 0;
  z-index: 1000;
}

nav .navigation .container {
  border-bottom: 3px solid #8C8C8C;
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 10px;
  text-transform: uppercase;
}

nav .navigation .container a {
  color: #4c4c4c;
  letter-spacing: 1px;
  line-height: 18px;
  padding: 10px;
}

nav .navigation .container a:not(:nth-child(6)) {
  border-bottom: 1px solid #B6B6B6;
}

nav .navigation .sub-navigation {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 10px;
}

nav .navigation .sub-navigation a {
  color: #4c4c4c;
  line-height: 18px;
  padding: 10px;
}

nav .navigation .sub-navigation a:not(:nth-child(7)) {
  border-bottom: 1px solid #B6B6B6;
}

nav .search-navigation {
  background-color: #e6e7e8;
  position: fixed;
  right: 0;
  top: 48px;
  height: calc(100% - 47px);
  margin-bottom: 150px;
  overflow-y: auto;
  -webkit-transition-duration: 1s;
  transition-duration: 1s;
  width: 0;
  z-index: 1000;
}

nav .search-navigation .search-box {
  border-bottom: 3px solid #8C8C8C;
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 10px;
  text-transform: uppercase;
}

nav .search-navigation .search-box a {
  color: #4c4c4c;
  letter-spacing: 1px;
  line-height: 18px;
  padding: 10px;
}

nav .search-navigation .search-items {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 10px;
}

nav .search-navigation .search-items a {
  color: #4c4c4c;
  line-height: 18px;
  padding: 10px;
}

nav .search-navigation .search-items a:not(:nth-child(3)) {
  border-bottom: 1px solid #B6B6B6;
}

.border-right {
  border-right: 1px solid #777777;
  padding-right: 10px;
}

.padding-right {
  padding-right: 5px;
}
